@import url('https://fonts.googleapis.com/css2?family=Alegreya+Sans+SC:wght@100&family=Bebas+Neue&family=Black+Han+Sans&family=Dongle&family=Merriweather:ital,wght@1,700&family=Orbitron:wght@500&family=Overpass:wght@400;800&display=swap');

.content{
    flex:1;width: 100%;text-align: center;margin:10px;
    border: 1px solid rgba(255,255,255,0.5);box-sizing: border-box;background: rgba(255,255,255,0.1);
    opacity: 0.7;overflow: hidden;cursor: pointer;transition: all 0.5s;display: flex;align-items: center;

}
.content:hover{background: rgba(255,255,255,0.15); transform: scale(1.05); opacity: 1;}
.content > img{width: 30%;}
.article{width: 70%;display: flex;flex-direction: column;justify-content: center;}
.title{letter-spacing: 2px; transition: all 0.5s;}
.content:hover .title {color: lightcyan; text-shadow: 0px 0px 10px aqua;}

.p{font: 18px/1.3 "arial"; color: #ddd; margin-top:5px;}