.wrap{position: fixed; width: 98%; height: 100%; display: flex; justify-content: center;align-items: center;}
.wrap > video{width: 98%; height: 100%; object-fit: cover; position: fixed; opacity: 0.75;}
.circle{width: 40%; height: 60%;}
.circle article{position: relative}
.circle article h1{position: absolute; font-size: 30px; opacity: 1; top:-60px; transition: all 1s ease-in-out;cursor: pointer; z-index: 1000;}

